<template>
  <Container :title="title">
    <v-form :advanced="isEdit" @submit="submit">
      <template slot="base">
        <group :disabled="isEdit" :label="lang.username" type="input" v-model="user.username"/>

        <group v-if="!isEdit" :label="lang.password" type="password" v-model="user.password" />
        <group v-if="!isEdit" :label="lang.repass" type="password" v-model="user.repass" />
        <group :label="lang.nickname" type="input" v-model="user.nickname" />
        <group :label="lang.email" type="input" v-model="user.email" />
        <group :label="lang.mobile" type="input" v-model="user.mobile" />
        <group :label="lang.status" type="status" v-model="user.status" />
      </template>
      <template  slot="advanced">
        <group type="image" :label="lang.avatar" v-model="user.avatar" />
        <group type="select" :label="lang.sex" :options="sex_options" v-model="user.sex" />
        <group type="input" :label="lang.qq"  v-model="user.qq" />

        <group type="date" :label="lang.birthday" v-model="user.birthday" />
        <group type="editor" :label="lang.info" v-model="user.info"/>
      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"
  export default {

    components: {Group, VForm, Container },
    data(){
      return {
        title:this.$t('user.editTitle'),
        advanced:true,
        lang:this.$t('user'),
        isEdit:false,
        sex_options: [
          {value:0, text:'男'},
          {value:1, text:'女'}
        ]
      }
    },
    computed:{
      user:{
        set(value){
          this.$store.commit('user/SET_USER', value)
        },
        get(){
          return this.$store.state.user.user
        }
      }
    },
    created(){
      const mid = this.$route.query['id']
      if (mid){ this.isEdit = true}
      this.$store.dispatch('user/fetchUser',mid)
    },
    methods:{
      submit(){
        if (this.user.password !== this.user.repass){
          this.$toast.warn({
            title:'form submit error ~',
            message: 'password is equal of repass !'
          })
          return
        }
        this.$store.dispatch('user/editUser').then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



